import { z } from 'zod';

export const AddVacationRequestSchema = z.object({
    start_date: z.date(),
    end_date: z.date(),
    vacation_type: z.string().min(1, 'Vacation type field is required.'),
    reason: z.string().min(1, 'Reason field is required.'),
});

export type AddVacationRequestFormValues = z.infer<typeof AddVacationRequestSchema>;
